import React, { Component } from 'react';
import { NavBar,InputItem, TextareaItem,Button } from 'antd-mobile';
import AvatarSelector from '../../component/avatar-selector/avatar-selector'
import {connect} from 'react-redux'
import {update} from '../../redux/user.redux.js'
import {Redirect} from 'react-router-dom'
@connect(
    state => state.user,
    {update}
)

class BossInfo extends Component{
    constructor(){
        super()
        this.state = {
            title:'',
            avatar:'',
            desc:'',
            company:'',
            money:''
        }
    }
    onChange(key,value){
        this.setState({
            [key]:value
        })
    }
    selectAvatar(imgName){
        this.setState({
            avatar:imgName
        })
    }
    render(){
        const path = this.props.location.pathname;
            const redirect = this.props.redirectTo
        return (
            //bossinfo bossinfo
            
            <div>
                {redirect&&redirect!==path?<Redirect to={this.props.redirectTo} /> : null}
                <NavBar mode="dark">Boss</NavBar>
                <AvatarSelector selectAvatar = {this.selectAvatar.bind(this)}></AvatarSelector>
                <InputItem onChange={v=>this.onChange('title',v)}></InputItem>
                <InputItem onChange={v=>this.onChange('company',v)}></InputItem>
                <InputItem onChange={v=>this.onChange('money',v)}></InputItem>
                 <TextareaItem title="" rows={3} autoHeight onChange={v=>this.onChange('desc',v)}/>
                 <Button type='primary' onClick={()=>{this.props.update(this.state)}}></Button>
            </div>
        )
    }
}


export default BossInfo



