import React, { Component } from 'react';
import {List,InputItem,WingBlank,WhiteSpace,Button,Radio} from 'antd-mobile'
import Logo from '../../component/logo/logo'
import {Redirect} from 'react-router-dom'
import {connect} from 'react-redux'
import {register} from '../../redux/user.redux.js'

@connect(
    state => state.user,
    {register}
)


class Register extends Component{
    constructor(props){
        super(props)
        this.state = {
            user:'',
            pwd:'',
            repeatPwd:'',
            type:'genius' //boss
        }
    }
    componentDidMount(){

    }

    handleChange(key,value){
        this.setState({
            [key]:value
        })
    }

    handleRegister(){
        console.log(this.state) //this.state
        //connectthis.props

        console.log(this.props)
        this.props.register(this.state)

    }

    register(){

    }
    render(){
        const RadioItem = Radio.RadioItem
        return (
            <div>
                {this.props.redirectTo?<Redirect to={this.props.redirectTo} /> : null}
                <Logo></Logo>
                <h2></h2>
                <WingBlank>
                    <List>
                        {this.props.msg ? <p className='errorMsg'>{this.props.msg}</p> : null}
                        <InputItem onChange={v=>this.handleChange('user',v)}>
                        </InputItem>
                        <WhiteSpace />
                        <InputItem onChange={v=>this.handleChange('pwd',v)}></InputItem>
                        <WhiteSpace />
                        <InputItem onChange={v=>this.handleChange('repeatPwd',v)}></InputItem>
                        <WhiteSpace />
                        <RadioItem checked={this.state.type === 'genius'}
                        onChange={()=>this.handleChange('type','genius')}>
                            
                        </RadioItem>
                        <WhiteSpace />
                        <RadioItem checked={this.state.type === 'boss'} 
                        onChange={()=>this.handleChange('type','boss')}>
                            BOSS
                        </RadioItem>
                    </List>
                    <Button type='primary' onClick={this.handleRegister.bind(this)}></Button>
    
                </WingBlank>
            </div>
        )
    }
}

export default Register



